.pet-card-container {
    container-type: inline-size;
    container-name: card;
    
    & .pet-card {
        background-color: white;
        border-radius: 16px;
        overflow: hidden;
        box-shadow: 5px 5px 21px 1px rgba(0,0,0,0.65);
        -webkit-box-shadow: 5px 5px 21px 1px rgba(0,0,0,0.65);
        -moz-box-shadow: 5px 5px 21px 1px rgba(0,0,0,0.65);
        transition: transform 0.3s ease, box-shadow 0.3s ease;
        
        &:hover {
            transform: translateY(-5px);
            box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
        }
        
        & .pet-image-container {
            position: relative;
            height: 180px;
            overflow: hidden;
            
            & .pet-image {
                width: 100%;
                height: 100%;
                overflow: hidden;
                position: relative;
                
                & img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
                
                &::after {
                    content: "";
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    height: 30px;
                    background-color: white;
                    border-radius: 50% 50% 1% 1%;
                }
            }
        }
        
        & .pet-info {
            padding: 15px 15px 20px;
            text-align: center;
            position: relative;
            z-index: 1;
            margin-top: -10px;
            
            & .pet-name {
                font-size: 18px;
                font-weight: 600;
                color: #333;
            }
        }
    }
}


@container card (max-width: 200px) {
    .pet-card {
        & .pet-image-container {
            height: 140px;
            
            & .pet-image::after {
                height: 20px;
            }
        }
        
        & .pet-info {
            padding: 10px 10px 15px;
            
            & .pet-name {
                font-size: 16px;
            }
        }
    }
}

@container card (min-width: 300px) {
    .pet-card {
        & .pet-image-container {
            height: 200px;
            
            & .pet-image::after {
                height: 35px;
            }
        }
        
        & .pet-info {
            & .pet-name {
                font-size: 20px;
            }
        }
    }
}

