.form-container.active,
.volunteer-form.active,
.donate-form.active,
.breeds-modal.active  {
  animation: popIn 0.25s ease;
}
@keyframes popIn {
  from {
    transform: translate(-50%, -50%) scale(0.95);
    opacity: 0;
  }
  to {
    transform: translate(-50%, -50%) scale(1);
    opacity: 1;
  }
}

.form-container,
.overlay {
  opacity: 0;
  transition: opacity 0.3s ease;
}

.form-container.active,
.overlay.active {
  display: block;
  opacity: 1;
}

.form-container {
  position: fixed;
  display: none;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); /* âœ… Centers both horizontally and vertically */
  width: 90%;
  max-width: 600px;
