  left: 50%;
  transform: translate(-50%, -50%);
  background: #fff;
  width: 1200px;
  max-width: 90%;
  border-radius: 10px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.3);
  padding: 20px;
  z-index: 1001;
  display: none;
}

 .masonry-gallery {
    max-width: 1200px;
    margin: 0 auto;
    max-height: 80vh;       /* limit height to 80% of viewport */
    overflow-y: auto;       /* enable vertical scrolling */
    padding-right: 10px;    /* space for scrollbar */
    scroll-behavior: smooth;
    scrollbar-width: thin; /* âœ… For Firefox */
    scrollbar-color: #ccc transparent; /* âœ… For Firefox */

    

    .gallery-title {
      text-align: center;
      font-size: 2rem;
      color: #444;
      margin-bottom: 20px;
    }

    #loader {
      text-align: center;
      color: #666;
      margin-bottom: 20px;
      font-style: italic;
    }

    .gallery {
      column-count: 4;
      column-gap: 20px;

      .gallery-item {
        position: relative;
        margin-bottom: 20px;
        overflow: hidden;
        border-radius: 10px;
        cursor: pointer;
        display: inline-block;
        width: 100%;
        transition: transform 0.3s ease;

        &:hover {
          transform: translateY(-4px);
        }

        img {
          width: 100%;
          border-radius: 10px;
          display: block;
          object-fit: cover;
          transition: transform 0.3s ease;

          &:hover {
            transform: scale(1.05);
          }
        }

        .info {
          position: absolute;
          bottom: 0;
          left: 0;
          right: 0;
          background: rgba(0, 0, 0, 0.6);
          color: #fff;
          padding: 10px;
          text-align: center;
          opacity: 0;
          transition: opacity 0.3s ease;

          h3 {
            margin: 0;
            font-size: 1rem;
          }

          p {
            margin: 5px 0 0;
            font-size: 0.9rem;
          }
        }

        &:hover .info {
          opacity: 1;
        }
      }

      @media (max-width: 1200px) {
        column-count: 3;
      }

      @media (max-width: 900px) {
        column-count: 2;
      }

      @media (max-width: 600px) {
        column-count: 1;
      }
    }
  }

