        overflow-y: auto;

        .vet-item {
          padding: 12px;
          border-bottom: 1px solid #ddd;
          cursor: pointer;
          border-radius: 8px;
          transition: background 0.3s ease;

          &:hover {
            background: #e9f3ff;
          }

          &.active {
            background: #cfe7ff;
            font-weight: bold;
          }

          h3 {
            margin: 0;
            font-size: 1.1rem;
            color: #222;
          }

          p {
            margin: 5px 0;
            font-size: 0.9rem;
            color: #555;
          }

          span {
            font-size: 0.85rem;
            color: #0078d7;
          }
        }

        &::-webkit-scrollbar {
          width: 8px;
        }
        &::-webkit-scrollbar-thumb {
          background: #ccc;
          border-radius: 4px;
        }
      }

      #vet-map {
        border-radius: 10px;
        width: 100%;
        height: 100%;
      }
    }

    @media (max-width: 800px) {
      .vet-container {
        grid-template-columns: 1fr;
        height: auto;
        max-height: 80vh;       /* limit height to 80% of viewport */
        overflow-y: auto;       /* enable vertical scrolling */
        padding-right: 10px;    /* space for scrollbar */
        scroll-behavior: smooth;
        scrollbar-width: thin; /* âœ… For Firefox */
        scrollbar-color: #ccc transparent; /* âœ… For Firefox */
        #vet-map {
          height: 400px;
        }
      }
    }

